.espace-cclient,
.espace-client {
  width: 100%;
  background: linear-gradient(#091f09, #091f09),
    linear-gradient(#1c1c1c, #1c1c1c), #1c1c1c;
  overflow: hidden;
}
.espace-client {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  display: none;
}
.espace-cclient {
  height: 1992px;
  position: relative;
  line-height: normal;
  letter-spacing: normal;
}
